import { useSelector } from "react-redux"
import { Link } from "react-router-dom"

function Store(){
const tuotteet = useSelector(state => state.products)

let myArr = []

for(let i=0;i<tuotteet.length;i++){
myArr.push(tuotteet[i].categoria)
}

let unique = [...new Set(myArr)];


const mapper = unique.map((a) => {
    let myArr2 = []
    for(let i=0;i<tuotteet.length;i++){
        if(a === tuotteet[i].categoria){
            myArr2.push(tuotteet[i].kuva)
        }
    }
    let new_arr = myArr2.slice(0, 4);

    return(
        <Link className="linkStyle" to={`/kauppa/${a}`}>
            <div key={a} className="grid-item">
            <h2>{a}</h2>
            {new_arr.map(a =>{
                return(
                    <img className="storeImg" src={a} alt="test"  />
                )
            } )}
        </div>
        </Link>
    )
})

        return(<>
<div className="kauppa">
    <h3> Ostaminen ei viel toimi tll sivulla mutta linkist lytyy toimiva kauppa.</h3><br></br>
    <a href="https://holvi.com/shop/hyvinvointikeskusluxus/">https://holvi.com/shop/hyvinvointikeskusluxus/</a>
    </div>
<div className="grid-container">
{mapper}
</div>
    </>
)
}

export default Store