import { useParams, useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { useState } from "react";

function StoreOne(){
    const id = useParams().id
    const products = useSelector(state => state.products)
    const product = products.find(product => product.id === id)
    const [loader,setLoader] = useState(null)
    const navigate = useNavigate();
    function goBack(){
		navigate(-1);
	}
    if(product && !loader){
        setLoader(true)
      }
 const xd = "<--";
if(loader){
    return(
        <div className="singleProduct">
            <button onClick={goBack} className="myButton2"> {xd} takaisin </button>
            <h1>{product.tuote}</h1>
            <h4>Kuvaus: {product.kuvaus}</h4>
            <h4>Hinta: {product.hinta}</h4>
            {product.mr > 99 ? <></> : <h3>jljell: {product.mr}</h3>}
            <img src={product.kuva} alt="tuote" className="singleStoreImg"/>
        </div>
    )}
   else {
    return(
        <div>
                unexpected error happened. please reload page
            </div>
    )
   } 
}

export default StoreOne