import { useParams,useNavigate } from "react-router-dom"
import { useSelector } from "react-redux" 
import { Link } from "react-router-dom"


function StoreFilter(){
    const filter = useParams().filter
    const products = useSelector(state => state.products)
    const results = products.filter(obj => {
        return obj.categoria === filter
    })
    const navigate = useNavigate();
    function goBack(){
		navigate(-1);
	}
    const xd = "<--"
const mapper = results.map(x=> {
    return(
        <Link className="linkStyle" to={`/tuote/${x.id}`}>
            <div key={x.id} className="grid-item">
            <h2>{x.tuote}</h2>
            <h4 style={{color:"white"}}>Hinta: {x.hinta} </h4>
            <img src={x.kuva} alt="tuote" className="storeImgBig" />
        </div>
        </Link>
    )
})

    return(
        <>
        <button onClick={goBack} className="myButton3"> {xd} takaisin </button>
        <div className="grid-container">
        {mapper}
        </div>
        </>
    )
}

export default StoreFilter