            <p>Alle 24 tuntia peruutettu ajanvaraus - veloitetaan ajanvarauksesta normaali hoitohinta. Peruutukset tulee siis tehd 24 h ennen varattua aikaa. Peruuttamattomat, unohdetut ajat tai peruutukset, jotka tehty alle 24 h ennen hoitoa veloitetaan tysimrisesti. Uutta aikaa ei voi varata seuraavalle 24 tunnille.</p>
