import { configureStore } from "@reduxjs/toolkit";
import productSlice from "./reducers/productReducer";
import galleriaSlice from "./reducers/galleriaReducer"
import eventSlice from "./reducers/eventReducer";
import notifSlice from "./reducers/notificationReducer";
import userSlice from "./reducers/userReducer"
import currentUserSlice from "./reducers/currentUserReducer"
import palauteSlice from "./reducers/palauteReducer"
const store = configureStore({
    reducer: {
        products: productSlice,
        galleria: galleriaSlice,
        events: eventSlice,
        notifications: notifSlice,
        users: userSlice,
        currentUser: currentUserSlice,
        palaute: palauteSlice

    }
})
export default store