import {createSlice} from "@reduxjs/toolkit"
import eventService from "../services/events"

const eventSlice = createSlice({
    name: "events",
    initialState: [],
    reducers:{
        setEvents:(state, action) =>{
            return action.payload
          },
          deleteEvent(state, action){
            const id = action.payload
            return state.filter(event => event.id !== id)
            },
            createEvent:(state,action) => {
                state.push(action.payload)
              },
              editEvent:(state,action)=>{
                const id = action.payload.id
                const blogToChange = state.find(n => n.id === id)
                const changedblog = {
                  ...blogToChange,
                  text: action.payload.text
                }
                return state.map(blog => blog.id !== id ? blog : changedblog)
              },
    }
})

export const {setEvents,deleteEvent,createEvent,editEvent} = eventSlice.actions

export const editoiTapahtumaa = (newObject, id) => {
  return async dispatch => {
    const updatedblog = await eventService.update(newObject, id)
   dispatch(editEvent(updatedblog))
  }
}

export const createEvents = content => {
    return async dispatch => {
      const newevent = await eventService.create(content)
      dispatch(createEvent(newevent))
    }
  }
  
  export const deleteEvents = (id) => {
      return async dispatch => {
       const deletedevent2 =  await eventService.remove(id)
       dispatch(deleteEvent(id))
      }
    }
    
export const initializeEvents = () => {
    return async dispatch => {
        const events = await eventService.getAll()
        dispatch(setEvents(events))
    }
}
  
export default eventSlice.reducer