import { useState, useEffect } from "react";
import CommentForm from "./CommentForm";
import Comment from "./Comment";
import { deletepalautes,createComment,editoiPalautetta } from "../reducers/palauteReducer";
import { useSelector, useDispatch } from "react-redux";
import Sider from "./Sider";
import { notice } from "../reducers/notificationReducer";
const Comments = () => {

    const dispatch = useDispatch()
    const userName = useSelector(state => state.currentUser)
    const palautteet = useSelector(state => state.palaute)

  
  const [currentUserId, setCurrentUserId] = useState(null)
  const [activeComment, setActiveComment] = useState(null);

  const rootComments = palautteet.filter(
    (backendComment) => backendComment.parentId === null
  );

  useEffect(() => {
   if(userName){
setCurrentUserId(true)}
else{
  setCurrentUserId(false)
}
},[userName]);

  const getReplies = (commentId) =>
    palautteet
      .filter((backendComment) => backendComment.parentId === commentId)
      .sort(
        (a, b) =>
          new Date(a.createdAt).getTime() - new Date(b.createdAt).getTime()
      );

      const addComment = (text, parentId=null) => {
      const newName = userName ? "Susanna" : "Vieras"
          dispatch(createComment(text,parentId,newName))
          dispatch(notice("kiitos palautteesta :)",5))
          setActiveComment(null);
      };




  const updateComment = (text, commentId) => {
    const updateTarget = palautteet.find(n => n.id === commentId)
    const updateObj = {
      body:text,
      username: updateTarget.username,
      userId: updateTarget.userId,
      parentId: updateTarget.parentId,
      id: updateTarget.id
    }
    dispatch(editoiPalautetta(updateObj,commentId))
      setActiveComment(null);
  }; 
  const deleteComment = (commentId) => {
    if (window.confirm("Haluatko varmasti poistaa kommentin?")) {
      dispatch(deletepalautes(commentId))
    }
  };


 
  return (
    <>
    <div className="etusivuComment">
    <div className="comments">
      <h3 className="comments-title">Tll voit antaa palautetta tai kysy asioita</h3>
      <div className="comment-form-title">Kirjoita kommenti</div>
      <CommentForm submitLabel="Kirjoita" handleSubmit={addComment} />
      <div className="comments-container">
        {rootComments.map((rootComment) => (
          <Comment
            key={rootComment.id}
            comment={rootComment}
            replies={getReplies(rootComment.id)}
            activeComment={activeComment}
            setActiveComment={setActiveComment}
            addComment={addComment}
            deleteComment={deleteComment}
            currentUserId={currentUserId}
            updateComment={updateComment}
          />
        ))}
      </div>
    </div>
    </div>
    <Sider></Sider>
    </>
  );
};

export default Comments;