import {createSlice} from "@reduxjs/toolkit"
import loginService from "../services/login"
import productService from "../services/products"

const currentUserSlice = createSlice({
    name: "currentUser",
    initialState: null,
    reducers:{
        setCurrentUser:(state, action)=>{
            return action.payload
        },
        logOut:(state,action)=>{
            return action.payload
        }
       
    }
})

export const {setCurrentUser,logOut} = currentUserSlice.actions




export const initializeCurrentUser = () => {
    return async dispatch => {
        const loggedUserJSON = window.localStorage.getItem("loggedproductAppUser")
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON)
      productService.setToken(user.token)
      dispatch(setCurrentUser(user))
    }
    }
  }
  export const loginUser = (username,password) => {
    return async dispatch =>{
    const user = await loginService.login({
               username: username,
               password: password
            })
        window.localStorage.setItem('loggedproductAppUser', JSON.stringify(user))
        productService.setToken(user.token)
        dispatch(setCurrentUser(user))
    }
   }
   
   export const logOutter = () => {
    return async dispatch => {
    window.localStorage.clear()
    dispatch(logOut(null))
}}

  export default currentUserSlice.reducer