import {createSlice} from "@reduxjs/toolkit"
import productService from "../services/products"

const productSlice = createSlice({
    name: "products",
    initialState: [],
    reducers:{
        setproducts:(state, action) =>{
            return action.payload
          },
          createProduct(state, action) {
            state.push(action.payload)
          },
          deleteProduct(state, action){
            const id = action.payload
            return state.filter(blog => blog.id !== id)
            },
            editProduct:(state,action)=>{
              const id = action.payload.id
              const blogToChange = state.find(n => n.id === id)
              const changedblog = {
                ...blogToChange,
                categoria: action.payload.categoria,
                hinta: action.payload.hinta,
                tuote:action.payload.tuote,
                kuva:action.payload.kuva,
                kuvaus: action.payload.kuvaus,
                mr:action.payload.mr,

              }
              return state.map(blog => blog.id !== id ? blog : changedblog)
            },
    }
})

export const {setproducts,createProduct,deleteProduct,editProduct} = productSlice.actions

export const editProduct2 = (newObject, id) => {
  return async dispatch => {
    const updatedblog = await productService.update(newObject, id)
   dispatch(editProduct(updatedblog))
  }
}

export const createProducts = content => {
    return async dispatch => {
      const newevent = await productService.create(content)
      dispatch(createProduct(newevent))
    }
  }
  
  export const deleteProducts = (id) => {
      return async dispatch => {
       const deletedevent2 =  await productService.remove(id)
       dispatch(deleteProduct(id))
      }
    }

export const initializeProducts = () => {
    return async dispatch => {
        const products = await productService.getAll()
        dispatch(setproducts(products))
    }
}
export default productSlice.reducer