import { useEffect } from 'react';
import { useDispatch} from "react-redux";
import { initializeProducts } from './reducers/productReducer';
import { initializeEvents } from './reducers/eventReducer';
import { initializeGallerias } from './reducers/galleriaReducer';
import { initializepalautes } from './reducers/palauteReducer';
import { initializeCurrentUser } from './reducers/currentUserReducer';
import {initializeUsers} from "./reducers/userReducer";
import ClientSide from './clientSide';
import { Route,Routes } from 'react-router-dom';
import Etusivu from "./components/Etusivu"
import Hyvinvointi from "./components/HyvinvointiPalvelut"
import Nettiajanvaraus from "./components/Nettiajanvaraus"
import Hinnasto from "./components/Hinnasto"
import Yhteystiedot from "./components/Yhteystiedot"
import Tapahtumat from "./components/Tapahtumat"
//imports from backside
//import SingleProduct from './userpanel/components/singleProducts';
import Dashboard from './userpanel/Dashboard';
//import GalleriaBack from './userpanel/components/GalleryBack';
//import KauppaBack from './userpanel/components/KauppaBack';
//import Events from './userpanel/components/events';
//import NewProduct from './userpanel/components/newProduct';
//import LoginForm from './userpanel/components/loginform';
//import SingleGallery from './userpanel/components/singleGallery';
import Store from './components/store';
import StoreFilter from './components/StoreFilter';
import StoreOne from './components/StoreSingle';
import CV from "./components/CV"
import EAH from "./components/Eah-koulutus"
import Galleria from './components/Gallery';
import Tietosuoja from './components/Tietosuoja';
import Comments from './components/Comments';

import { lazy } from 'react';

//const Dashboard = lazy(()=> import('./userpanel/Dashboard'))
const GalleriaBack = lazy(()=> import("./userpanel/components/GalleryBack"))
const KauppaBack = lazy(()=> import("./userpanel/components/KauppaBack"))
const Events = lazy(()=> import("./userpanel/components/events"))
const SingleProduct = lazy(()=> import("./userpanel/components/singleProducts"))
const NewProduct = lazy(()=> import("./userpanel/components/newProduct"))
const LoginForm = lazy(()=> import("./userpanel/components/loginform"))
const SingleGallery = lazy(()=> import("./userpanel/components/singleGallery"))


function App(){
const dispatch = useDispatch()

useEffect(() => {
  dispatch(initializeProducts())
  dispatch(initializeEvents())
  dispatch(initializeGallerias())
  dispatch(initializeCurrentUser())
  dispatch(initializeUsers())
  dispatch(initializepalautes())
},[dispatch])

    return(
      <>
      <Routes >
        <Route path='/'element={ <ClientSide />}>
          <Route path="*" element={<Etusivu />}  />
          <Route path="/hyvinvointipalvelut" element={<Hyvinvointi />}  />
          <Route path="/nettiajanvaraus" element={<Nettiajanvaraus />}  />
          <Route path="/eah" element={<EAH />}  />
          <Route path="/hinnasto" element={<Hinnasto />}  />
          <Route path="/yhteystiedot" element={<Yhteystiedot />}  />
          <Route path="/tapahtumat" element={<Tapahtumat />}  />
          <Route path="susanna" element={<CV />}  />
          <Route path="kauppa" element={<Store />}  />
          <Route path="kauppa/:filter" element={<StoreFilter />}  />
          <Route path="tuote/:id" element={<StoreOne />}  />
          <Route path="galleria" element={<Galleria />}  />
          <Route path="tietosuojaseloste" element={<Tietosuoja />}  />
          <Route path="palaute" element={<Comments />}  />
        </Route>

      <Route path='/control' element={<Dashboard />}>
        <Route path="/control/gallery" element={<GalleriaBack />}  />
        <Route path="/control/kauppa" element={<KauppaBack />}  />
        <Route path="/control/event" element={<Events />} />
        <Route path="/control/uusituote" element={<NewProduct />} />
        <Route path="/control/kauppa/:id" element={<SingleProduct />} />
        <Route path="/control/galleria/:id" element={<SingleGallery />} />
        <Route path="/control/login" element={<LoginForm />} />
        </Route>
     </Routes>
     </>
    )
}

export default App
