import {createSlice} from "@reduxjs/toolkit"
import galleriaService from "../services/galleria"

const galleriaSlice = createSlice({
    name: "galleria",
    initialState: [],
    reducers:{
        setGalleria:(state, action) =>{
            return action.payload
          },
          deleteGalleria(state, action){
            const id = action.payload
            return state.filter(event => event.id !== id)
            },
            createGalleria:(state,action) => {
                state.push(action.payload)
              },editGalleria:(state,action)=>{
                const id = action.payload.id
                const blogToChange = state.find(n => n.id === id)
                const changedblog = {
                  ...blogToChange,
                  text: action.payload.text,
                  img: action.payload.img
                }
                return state.map(blog => blog.id !== id ? blog : changedblog)
              },
    }
})

export const {setGalleria,createGalleria,deleteGalleria,editGalleria} = galleriaSlice.actions

export const editoiGalleriaa = (newObject, id) => {
  return async dispatch => {
    const updatedblog = await galleriaService.update(newObject, id)
   dispatch(editGalleria(updatedblog))
  }
}

export const createGallerias = content => {
    return async dispatch => {
      const newevent = await galleriaService.create(content)
      dispatch(createGalleria(newevent))
    }
  }
  
  export const deleteGallerias = (id) => {
      return async dispatch => {
       const deletedevent2 =  await galleriaService.remove(id)
       dispatch(deleteGalleria(id))
      }
    }

export const initializeGallerias = () => {
    return async dispatch => {
        const gallerias = await galleriaService.getAll()
        dispatch(setGalleria(gallerias))
    }
}
export default galleriaSlice.reducer