import {Link} from "react-router-dom"
import Sider from "./Sider"


function Etusivu(){
    return(
        <div className="etusivu">
            <p>Hei, kiva ett lysit sivuilleni! Tll Hyvinvointikeskus Luxusessa tyskentelee ja Sinua
palvelee nkijn, parantajan ja koulutetun hierojan yli 20 vuosikymmenen kokemuksella ja
ammattitaidoilla Susanna Hijer. Toivotan Sinut tervetulleeksi avaamaan oven omaan hyvinvointiisi saavuttamaan tasapainon,
eheyden ja ilon elmsi.</p><br></br>
<p><span className="bold">Hyvinvointipalveluillani</span>pyritn lytmn ydinsyyt siit, miksi terveys tai elmn muut eri
osa-alueet ovat eptasapainossa. Palveluissa toimii lhihoidot vastaanotolla Sastamalassa
sek mys ethoidot.</p>
<p>Palveluni on suunnattu sinulle , joka
haluat muutosta elmsi ja list hyvinvointia arkeesi.</p>
<p><span className="bold">Verkkokaupasta</span>voit ostaa hyv oloa kuten kursseja, lahjakortteja, hoitoja, hoitavia nitteit
sek uniikkeja ksintehtyj Ailakorun koruja.</p>
<p><span className="bold">Ajanvaraukset</span>hoidat helpoiten <Link to ="nettiajanvaraus" className="cringe">nettiajanvarauksesta. </Link>Mys tekstiviestill ja shkpostitse voit
kysell aikoja.</p><br></br><p>Tapaamisiin vastaanotolla ja etn!<span className="cringe">Ja hei muistathan, ett</span><span className="cringe2">Sin itse olet oman elmsi luksus!</span></p>
<br></br>
<Link to ="yhteystiedot" className="cringe">Ota yhteytt ja kysy lis!</Link>
            <br></br>
            <div className="kuva-container">
            <div className="left-container">
            <img className="etusivu-kuva" src="https://i.imgur.com/7eePvN8.jpg" alt="kuva"></img>
            </div>
            <div className="right-container">
            <p className="etusivu-kuva-text">Valolla ja rakkaudella, minulla on ilo palvella Sinua hyvinvointisi matkalla!
            <br></br>
            Susanna Hijer
            <br></br>
            Parantaja, Nkij, Kouluttaja, Theta Healing Practioner, Koulutettu hieroja</p>
            </div>
            <div>
                <img className="etusivu-kuva2" src="https://i.imgur.com/6Qa8W4R.jpeg" alt="etusivu-kuva2"></img>
            </div>
            </div>
            <Sider />
        </div>
    )
}
export default Etusivu