import { useSelector } from "react-redux"

function Galleria(){
const data = useSelector(state => state.galleria)

    const gallery = data.map(x =>{
        return(
            <div key={x.id}>
                <h3>{x.text}</h3>
                <img className="galleria-img" src={x.img} alt={x.id}></img>
            </div>
        )
    })
    return(
        <div className="galleriaDiv">
       {gallery}
    </div>
    )
}
export default Galleria