import {createSlice} from "@reduxjs/toolkit"
import palauteService from "../services/palaute"

const palauteSlice = createSlice({
    name: "palaute",
    initialState: [],
    reducers:{
        setpalautes:(state, action) =>{
            return action.payload
          },
          deletepalaute(state, action){
            const id = action.payload
            return state.filter(palaute => palaute.id !== id)
            },
            createpalaute:(state,action) => {
                state.push(action.payload)
              },
              createVastaus:(state,action) => {
                const id = action.payload.id
                const comment = action.payload.vastaus
                const palauteToChange = state.find(n => n.id === id)
                const changedPalaute = {
                    ...palauteToChange,
                    vastaus: palauteToChange.vastaus = comment
                  }
                  return state.map(palaute => palaute.id !== id ? palaute : changedPalaute)
              },
              editPalaute:(state,action)=>{
                const id = action.payload.id
                const blogToChange = state.find(n => n.id === id)
                const changedblog = {
                  ...blogToChange,
                  body: action.payload.body
                }
                return state.map(blog => blog.id !== id ? blog : changedblog)
              },
    }
})

export const {setpalautes,deletepalaute,createpalaute,editPalaute} = palauteSlice.actions

export const editoiPalautetta = (newObject, id) => {
  return async dispatch => {
    const updatedblog = await palauteService.update(newObject, id)
   dispatch(editPalaute(updatedblog))
  }
}

  export const createComment =  (text,parentId,userName) => {
    return async dispatch => {
      const content = {
      body: text,
      parentId,
      userId: "1",
      username: userName,
    } 

  const commentObj = await palauteService.create(content)
  dispatch(createpalaute(commentObj))
    };
  };
  
  export const deletepalautes = (id) => {
      return async dispatch => {
       const deletedpalaute2 =  await palauteService.remove(id)
       dispatch(deletepalaute(id))
      }
    }
    
export const initializepalautes = () => {
    return async dispatch => {
        const palautes = await palauteService.getAll()
        dispatch(setpalautes(palautes))
    }
}
  
export default palauteSlice.reducer